import { inProgKey } from "./storageConsts"
function generateMarksmanshipData(hitCountData){
    return hitCountData.map((hitCountDataObj, i) =>{
        const {targetName} = hitCountDataObj
        return {
                targetName,
                head:{
                    a:'',
                    c:''
                },
                torso:{
                    a:'',
                    c:'',
                    d:'',
                },
            }
            })

}

export default function startNewCourseOfFire({cofId, hitCountData}){
    const currentInProg = JSON.parse(localStorage.getItem(inProgKey) || '{}')
    localStorage.setItem(inProgKey, JSON.stringify({...currentInProg, [cofId]:{time:[],marksmanship:generateMarksmanshipData(hitCountData)}}))
}

