import { Link, useParams, useNavigate } from "react-router-dom";
import { LinkButton } from "../components/Buttons";
import startNewCourseOfFire from "../storage/startNewCourseOfFire";
import { LinkWrapper } from "../components/Links";

export default function Setup({getPageData}) {
    const {cofId} = useParams()
    const pageData = getPageData(cofId)
    const { hitCountData } = pageData
    const navigate = useNavigate()

    function startCourseOfFire(){
      startNewCourseOfFire({cofId, hitCountData})
      navigate(`/cof/${cofId}/0`)
    }

    return (
      <main style={{ padding: "1rem 0" }}>
        <h2 style={{ color:'#CEFF51' }}>Setup</h2>
        <img style = {{maxWidth:'100%'}} src = {pageData.setupPic}/>
        <div>
          <p>{pageData.description}</p>
        </div>
        <LinkWrapper>
            <LinkButton onClick={startCourseOfFire}>Start Course of fire</LinkButton>
        </LinkWrapper>
        <LinkWrapper>
            <Link to={`/history/${cofId}`}>View History</Link>
        </LinkWrapper>
        <LinkWrapper>
            <Link to={`/`}>Home</Link>
        </LinkWrapper>
      </main>
    );
  }